import React, {Component} from 'react';
import emailjs from 'emailjs-com';


export default class ContactUs extends Component {
    render() {
        function sendEmail(e) {
            e.preventDefault();

            emailjs.sendForm('default_service', 'template_z9zlm01', e.target, 'user_FhEWKM5IXCkmUoOqe2yTB')
                .then((result) => {
                    console.log(result.text);
                    alert("Email sent successfully! I'll get back to you ASAP")
                }, (error) => {
                    console.log(error.text);
                    alert("Failed!"+error)
                });
        }

        return (
            <section id="contact">
                <div className="row section-head">
                    <div className="two columns header-col">
                        <h1><span>Get In Touch.</span></h1>
                    </div>
                    <div className="ten columns">
                        <p className="lead"> Please call or email me for internships and entry level positions in
                            penetration testing/cyber-security , web-development, or software engineering.
                        </p>
                    </div>
                </div>
                <div className="row">
                    <div className="eight columns">
                        <form className="contact-form" onSubmit={sendEmail}>
                            <input type="hidden" name="contact_number" />
                            <label>Name</label>
                            <input type="text" name="user_name" />
                            <label>Email</label>
                            <input type="email" name="user_email" />
                            <label>Message</label>
                            <textarea name="message" />
                            <div>
                                <button className="submit">Submit</button>
                                                  <span id="image-loader">
                                  <img alt="" src="images/loader.gif"/>
                                <input type="submit" value="Send" />
                                                  </span>
                            </div>
                        </form>
                         {/*Form End*/}
                         {/*contact-warning*/}
                        <div id="message-warning"> Error boy</div>
                         {/*contact-success*/}
                        <div id="message-success">
                            <i className="fa fa-check"/>Your message was sent, thank you!<br/>
                        </div>
                    </div>
                    <aside className="four columns footer-widgets">
                        <div className="widget widget_contact">
                            <h4>Address and Phone</h4>
                            <p className="address">
                                Andrew Alagna<br/>
                                Flushing, NY 11367 US<br/>
                                <span>(917) 601-9404</span><br/>
                                aalagna04@gmail.com
                            </p>

                        </div>

                    </aside>
                </div>
            </section>
        );
    }
};
